import React from "react";
import Prompt from "./Prompt";

const MapConsoleOutput = ({ consoleOutput }) => {
  return (
    <div>
      {consoleOutput.map(({ command, result }, index) => (
        <div key={index}>
          <div>
            <Prompt />
            <span>{command}</span>
          </div>
          <div>
            {result}
          </div>
        </div>
      ))}
    </div >
  );
};

export default MapConsoleOutput;
